import React, {PureComponent} from "react";
import {connect} from "react-redux";
import moment from 'moment';
import exportExcel from '../../../utils';
import {Button, Col, message, Row, Table} from 'antd';
import {Layout} from "./style";
import *  as api from "../../../axios";
import ClickOutside from '../../../utils/clickOutside'
import AuthComponent from "../../common/authComponent ";
import ModalCheck from "../progressManagement/modalCheck";
import ModalExitCheck from "../progressManagement/modalExitCheck";
import LineWrap from '../../common/lineWrap'

const AuthButton = AuthComponent(Button);
let lastIndex = 0;

class MakingList extends PureComponent {
    //
    renderIndex = (text, row, index, lastIndex) => {
        if (index < lastIndex) {
            return index + 1;
        }
        return {
            children: '',
            props: {
                colSpan: 4
            }
        };
    }

    //
    renderContent = (value, row, index, lastIndex) => {
        const obj = {
            children: <LineWrap title={value}/>,
            props: {}
        };
        if (index === lastIndex) {
            obj.props.colSpan = 0;
        }
        return obj;
    };

    state = {
        stage: 1,
        stageProducedList: [],
        pedestalList: [],
        currentId: '',
        storageColumns: [
            {
                title: '',
                key: 'index',
                width: '10%',
                render: (text, row, index) => this.renderIndex(text, row, index, lastIndex)
            }, {
                title: '',
                dataIndex: 'name',
                width: '20%',
                render: (value, row, index) => this.renderContent(value, row, index, lastIndex)
            }, {
                title: '',
                dataIndex: 'status',
                width: '20%',
                render: (value, row, index) => this.renderContent(value, row, index, lastIndex)
            },
            {
                title: '',
                dataIndex: 'current',
                width: '15%',
                render: (value, row, index) => this.renderContent(value, row, index, lastIndex)
            },
            {
                title: '',
                dataIndex: 'bodyName',
                render: (value, row, index) => this.renderContent(value, row, index, lastIndex)
            },
            {
                title: '',
                dataIndex: 'total',
                width: '15%',
                render: (value) => value ? value : ''
            }
        ],
        producedColumns: [
            {
                title: '',
                key: 'index',
                width: 50,
                render:  (text, record, index) => index + 1
            }, {
                title: '',
                width: 100,
                dataIndex: 'bridgeName'
            }, {
                title: '',
                width: 150,
                dataIndex: 'mileage'
            }, {
                title: '',
                width: 100,
                dataIndex: 'typeName'
            }, {
                title: '',
                // width:100,
                dataIndex: 'name'
            }
        ],
        statusColumns: [],
        loading: false,
        checkVisible: false,
        exitCheckVisible: false,
        paramsDisappear: {},
        MaxIndex: 0
    };

    //
    handleRowClick = (row) => {
        this.setState({currentId: row.id});
        row.id && this.getStageProduced(row.id)
    };
    //
    setRowClassName = (record) => {
        return record.id === this.state.currentId ? 'curSelectRow' : '';
    }
    // 
    handleClickOutSide = () => {
        this.getStageProduced(null)
        this.setState({currentId: null})
    }

    // 
    handleCancel = () => {
        this.setState({
            // visible: false,
            // exitVisible: false,
            checkVisible: false,
            exitCheckVisible: false,
            // textAreaValue: ''
        });
    }

    // 
    handleCheckSetting(current) {
        this.formatAsyncFunc(current);
        this.setState({
            checkVisible: true,
        });
    }

    // 
    handleCheckExitSetting(current) {
        this.formatAsyncFunc(current);
        this.setState({
            exitCheckVisible: true,
        });
    }

    //
    handleColumns(stageProducedList) {
        //stageProducedList 
        const {states} = this.props.beamInfo.toJS();
        const {stage} = this.state
        const statusTitle = states.filter(item =>
            item.stage == stage
        ).map(cur => {
            return cur.name
        })

        this.setState({MaxIndex: statusTitle.length})
        stageProducedList.forEach(el => {
            for (let i = 0; i < el.progresses.length; i++) {
                const progress = el.progresses[i];
                const curProgress = states.find(state => state.id === progress.stateId);
                if (curProgress) {
                    // !title.includes(curProgress.name) && title.push(curProgress.name);
                    el[curProgress.name] = moment(progress.beginDate).format('YYYY-MM-DD')
                }
            } 
        });
        return statusTitle.map(item => ({
            title: item,
            dataIndex: item,
            width: 100,
            render: (text, record) => {
                return (
                    record.status !== 2 ?
                        <span onClick={() => this.handleCheckSetting(record)}
                              title={text ? moment(text).format('YYYY-MM-DD') : ''}>
                        {text ? moment(text).format('YYYY-MM-DD') : ''}
                            </span> :
                        <span onClick={() => this.handleCheckExitSetting(record)}
                              title={text ? moment(text).format('YYYY-MM-DD') : ''}>
                        {text ? moment(text).format('YYYY-MM-DD') : ''}
                            </span>
                    // moment(text).format('YYYY-MM-DD')
                )
            }

        }))
    };

    //
    getStagePedestral = () => {
        this.setState({
            loading: true
        });
        const {stage} = this.state
        api.getStagePedestral(stage).then(res => {
            const pedestalList = res.data.data.items;
            if (pedestalList && pedestalList.length) {
                const total = pedestalList.reduce((pre, next) => (pre + next.total), 0);
                pedestalList.push({total});
                lastIndex = pedestalList.length - 1;
                pedestalList.forEach(item =>
                    item.bodyName = item.bodyName ? item.bodyName : ''
                )
                this.setState({
                    pedestalList: pedestalList,
                });
            }
        })
    };
    //
    getStageProduced = (pedestalId) => {
        const {stage} = this.state;
        api.getStageProduced(stage, pedestalId).then(res => {
            if (res.data.code == 1) {
                const stageProducedList = res.data.data.items;
                const stageList = stageProducedList.map(item =>
                    ({
                        ...item,
                        currentId: pedestalId
                    })
                )
                this.setState({
                    stageProducedList: stageList,
                    loading: false,
                    statusColumns: this.handleColumns(stageList)
                })
            } else {
                message.warning(res.data.msg);
            }
        })
    };

    export = (stageProducedList, pedestalList, storageColumns, columns) => {
        const columnList = JSON.parse(JSON.stringify(columns));
        /* debugger;
        columnList.shift() */
        const sliceColumns = JSON.parse(JSON.stringify(storageColumns.slice(1, 3)))
        sliceColumns[0].dataIndex = 'pedestalName';
        sliceColumns[1].dataIndex = 'pedestalStatus';
        let indexList = [{title:'',width: 100,key:'index'}];
        columnList.shift();
        const insertColumns = indexList.concat(sliceColumns).concat(columnList);
        let pedestalLists = [];
        let stageProducedLists = [];
        // const sliceList = JSON.parse(JSON.stringify(pedestalList).replace(/name/g, "pedestalName").replace(/status/g, 'available'));

        // debugger
        // stageProducedList.forEach(item => {
        //     const currentItem = sliceList.find(cur => item.currentId === cur.id);
        //     item.pedestalName = currentItem.pedestalName
        //     item.available = currentItem.available
        // })
// console.log(stageProducedList);
        
        pedestalList.map((val,key)=>{
            pedestalLists.push({index:(key+1),...val})
        })
        stageProducedList.map((val1,key1)=>{
            stageProducedLists.push({index:(key1+1),...val1})
        })
        console.log(insertColumns,'insertColumns')
        const sheetList = [
            {
                headers: storageColumns,
                data: pedestalLists,
                sheetName: ''
            },
            {
                headers: insertColumns,
                data: stageProducedLists,
                sheetName: ''
            },
        ]
        exportExcel(sheetList, '.xlsx')
    }

    render() {
        const {
            loading, pedestalList, checkVisible, paramsDisappear,
            exitCheckVisible, stageProducedList, storageColumns, MaxIndex, producedColumns, statusColumns, stage
        } = this.state;
        const beamInfo = this.props.beamInfo.toJS();
        const authCode = this.props.authCode.toJS();
        let renderColumns = [];
        if (stage == 20) {
            renderColumns = storageColumns.filter(columns => columns.title != '')
        } else {
            renderColumns = storageColumns.filter(columns => columns.title != '')
        }
        const columns = [...producedColumns, ...statusColumns]

        const width = 500 + 100 * MaxIndex;
        console.log(width);
        return (
            <Layout>
                <div className='layout-header'>
                    {this.state.stage==1
                            ?<AuthButton type="primary" source="fun" authcode='77003001' authcodes={authCode.funAuthCode} {...beamInfo}
                                onClick={() => this.export(stageProducedList, pedestalList, renderColumns, columns)}>
                            </AuthButton>
                            :(this.state.stage == 10
                            ?(<AuthButton type="primary" source="fun" authcode='77008001' authcodes={authCode.funAuthCode} 
                            onClick={() => this.export(stageProducedList, pedestalList, renderColumns, columns)}>
                              </AuthButton>)
                            :(<AuthButton type="primary" source="fun" authcode='77009001' authcodes={authCode.funAuthCode}
                            onClick={() => this.export(stageProducedList, pedestalList, renderColumns, columns)}>
                            </AuthButton>))}
                </div>
                <Row gutter={10}>
                    <Col span={10}>
                        <ClickOutside onClickOutside={this.handleClickOutSide}>
                            <Table
                                rowKey="id"
                                loading={loading}
                                rowClassName={this.setRowClassName}
                                onRow={record => {
                                    return {
                                        onClick: () => this.handleRowClick(record)
                                    };
                                }}
                                columns={renderColumns}
                                dataSource={pedestalList}
                                bordered={true}
                                pagination={false}
                                scroll={{y: 600}}
                            />
                        </ClickOutside>
                    </Col>
                    <Col span={14}>
                        <Table
                            onSelect={this.handleSelect}
                            rowKey="id"
                            loading={loading}
                            columns={columns}
                            dataSource={stageProducedList}
                            bordered={true}
                            scroll={{x: width, y: 650}}
                            className="tableStyle"
                            pagination={false}/>
                    </Col>
                </Row>
                {/*    */}
                <ModalCheck
                    checkVisible={checkVisible}
                    paramsDisappear={paramsDisappear}
                    handleCancel={this.handleCancel}
                ></ModalCheck>
                {/*    */}
                <ModalExitCheck
                    exitCheckVisible={exitCheckVisible}
                    paramsDisappear={paramsDisappear}
                    handleCancel={this.handleCancel}
                ></ModalExitCheck>
            </Layout>
        )
    };

    componentDidMount() {
        this.setState({
            stage: this.props.history.location.query.stage
        }, async () => {
            this.getStagePedestral();
            this.getStageProduced(null);
        })
    }

    // 
    async formatAsyncFunc(current) {
        //   
        const {data: {data: next}} = await api.getBodyNext(current.id);
        if (!next) return;
        //   
        const {data: {data: {items: userList}}} = await api.getRemindUsers(next.stateId)
        if (!userList) return;
        //  
        const {data: {data: {items: pedestalList}}} = await api.getPedestalAvailable(next.stage);
        if (!pedestalList) return;
        // console.log(pedestalList);
        this.setState({
            paramsDisappear: {
                ...current,
                first: next.first,
                loading: false,
                stageName: next.stageName,
                stateName: next.stateName,
                stateId: next.stateId,
                stage: next.stage,
                beginDate: next.beginDate,
                remark: next.remark,
                userName: next.userName,
                userList: userList,
                pedestalList: pedestalList,
            }
        });
    }
}

const mapStateToProps = state => {
    return {
        beamInfo: state.getIn(["login", "beamInfo"]),
        authCode: state.getIn(["login", "authCode"]),
    };
};
export default connect(
    mapStateToProps,
    null
)(MakingList);